/**
 *  共用页面参数配置
*/
import defaultImg from '../../../../assets/standbypage.png'
import liveParam from './liveParam'
import FileSaver from 'file-saver'
// import getBase64Image from '../../../public/imgChangeBase64'

export default {
  name: "parameter_config",
  components: {
    liveParam
  },
  created () {
    this.netApi = this.$api().get('parameter_config');
    this.getConfig();
    // 编辑sn时
    if(this.$route.query.sn) {
      // 场景确定为编辑状态
      this.iseditScenec = true
      let index = this.$route.query.sn.indexOf(',')
      if(index == -1) {
        this.$http.get(this.netApi.queryParamInfo, {
          // params: {sn: '1234567890'}
          params: {sn: this.$route.query.sn}
        }).then(res => {
          // console.log(res.body.data)
          let editForm = res.body.data
          this.beforeHandPramData(editForm)
        }
        )
      }
    } else {
      let img = defaultImg;
      let image = new Image();
      image.src = img;
      let self = this
      image.onload = function() {
        let base64 = self.getBase64Image(image);
        self.AnimationList.push({name: '默认', url: base64})
      }
    }
    // console.log(this.$route)
  },
  mounted() {
    this.$EventBus.$on('reload', () => {
      window.location.reload();
    })
  },
  data () {
    return {
      netApi: null,
      // 待导出的json数据
      form: {
        lockTime: 10, // 锁屏时间
        checkSuccess: this.$t('parameter.validateSuccess'),
        checkFailure: this.$t('parameter.validateFailed'),
        blacklist: this.$t('parameter.noRight'),
        fullTime: true,  // 24小时制
        autoUpdate: false,
        phoneticHints: true,  // 语音提示
        autoUpgrade: false,
        cycle: false,
        timeInterval: 3,
        promptingTime: 2,
        scene: {
          id: '', // 唯一id
          name: '',
          antiFake: '', // 活体防伪相关参数
          checkValue: '', // 活体检测阈值
          similarity: '', // 相似度阈值
          visibleRay: '', // 可见光阈值
        }, // 场景相关信息
      },
      spiderTimes: {

      },
      lockTimeArr: [{label: '10s', time: 10}, {label: '30s', time: 30}, {label: '60s', time: 60}, {label: '永不', time: -1}],
      notApi: 0,
      languageSel: [], //语言选择
      timeZoneSel: [], //时区选择
      regionSel: [], //地区选择
      dateFormatSel: [], //日期格式选择
      impotTypeSel: [], //进出类型
      controlSel: [], // 门禁控制方式
      waySel: [], // 设备通行方式
      sceneSel: [], // 场景方式
      tipTime: '', // 提示时间
      passageWayOne: [], // 通行方式
      modelScene: '', // 场景方式
      logImageUrl: '', // logo图片
      AnimationList: [],
      activeNames: ["1", "2", "3"],
      promptingTimeSel: [{label: '1s', time: 1}, {label: '2s', time: 2}, {label: '3s', time: 3}, {label: '自定义', time: -1}],
      timeIntervalSel: [1, 2, 3, 4, 5],
      trafficSelectForm: {
        trafficSceneSelectRadio: []
      },
      fileList: [],
      allWeeks: [{
        name: this.$t('accessSystem.zhouyi'),
        timeList: [{timeQj: [0, 144]}]
      },
      {
        name: this.$t('accessSystem.zhouer'),
        timeList: [{timeQj: [0, 144]}]
      },
      {
        name: this.$t('accessSystem.zhousan'),
        timeList: [{timeQj: [0, 144]}]
      },
      {
        name: this.$t('accessSystem.zhousi'),
        timeList: [{timeQj: [0, 144]}]
      },
      {
        name: this.$t('accessSystem.zhouwu'),
        timeList: [{timeQj: [0, 144]}]
      },
      {
        name: this.$t('accessSystem.zhouliu'),
        timeList: [{timeQj: [0, 144]}]
      },
      {
        name: this.$t('accessSystem.zhouri'),
        timeList: [{timeQj: [0, 144]}]
      }],
      dialogScene: false,
      dialogScenceTitle: this.$t('parameter.fastScene'),
      preventFalse: true, // 默认有防伪
      liveThingVal: 0.5, // 活体防伪值
      liveThingTip: 50,
      NcapacityVal: 0.5, // 1:N识别
      NcapacityTip: 50,
      OneCapacityVal: 0.5, // 1:1识别
      OneCapacityTip: 50,
      iseditScenec: false, // 场景默认非编辑状态
      sceneType: 0,
      isUp: true, //是否满足上传标准
    }
  },
  watch: {
  },
  methods: {
    beforeAvatarUpload(file) {
      console.log(file)
      const isJPG = file.type === 'image/jpeg';
      const isLt2M = file.size / 1024 / 1024 < 0.5;
      if (file.type !== 'image/jpeg' && file.type !== 'image/png') {
        this.$message.error('上传头像图片只能是 JPG 和 PNG 格式!');
        this.isUp = false;
      }
      if (!isLt2M) {
        this.$message.error('上传头像图片大小不能超过 500kb!');
        this.isUp = false;
      }
      return isJPG && isLt2M
    },
    goBack() {
      // this.$router.go(-1);
      this.$router.push(`/deviceManage/deviceControl?back=1`)
    },
    initPraram() {
      this.AnimationList = []
    },
    changePramJsonFile(file) {
      console.log(file)
      const reader = new FileReader()
      reader.readAsText(file.raw)
      const _this = this
      reader.onload = function () {
        // debugger
        let result = JSON.parse(this.result)
        _this.beforeHandPramData(result)
      }
    },
    // 导出配置
    imoprtOutParam() {
      // 将json转换成字符串
      const data = JSON.stringify(this.form)
      const blob = new Blob([data], {type: ''})
      FileSaver.saveAs(blob, 'config.json')
    },
    // 编辑或者导入时对数据做预处理
    beforeHandPramData(initData) {
      // debugger
      this.initPraram()
      let editForm = initData
      console.log(editForm)
      // 时间段数据转换后复值
      this.allWeeks.forEach((item, index) => {
        this.allWeeks[index].timeList = []
      })
      let xqs = ['monday', 'tuesday', 'wednesday', 'thursday', 'friday', 'saturday', 'sunday']
      xqs.forEach((res, index) => {
        if(editForm[xqs[index]]) {
          editForm[xqs[index]].split(',').forEach((item, dex) => {
            let start = parseInt(item.split('-')[0].split(':')[0]) * 6 + parseInt(item.split('-')[0].split(':')[1]) / 10
            let end = parseInt(item.split('-')[1].split(':')[0]) * 6 + parseInt(item.split('-')[1].split(':')[1]) / 10
            this.allWeeks[index].timeList.push({timeQj: [start, end]})
          })
        }
      })
      // 通行方式
      if(editForm.passageWay) {
        this.passageWayOne = editForm.passageWay.split(',')
      }
      // 通行场景
      if(editForm.sceneId) {
        this.modelScene = editForm.sceneId
      }
      // logo图片
      if(editForm.logo) {
        this.logImageUrl = editForm.logo;
      }
      // this.form.logo = editForm.logo;
      // 待机图片
      const imgZd = ['pictureOne', 'pictureTwo', 'pictureThree']
      imgZd.forEach(res => {
        if(editForm.hasOwnProperty(res)) {
          this.AnimationList.push({name: '', url: editForm[res]})
        }
      })
      // 场景
      this.form = JSON.parse(JSON.stringify(initData))
    },
    // 保存设置
    saveFixed(val) {
      // sn号
      // this.form.sn = '1234567890'
      // if(this.$route.query.sn.indexOf(",") == -1) {
      //   this.form.sn = this.$route.query.sn
      // }
      this.form.sn = this.$route.query.sn
      if(this.famaterData()) {
        this.$http.post(this.netApi.addParamInfo, this.form).then(res => {
          if(res.body.status == 200) {
            this.$message.success(this.$t("common.modifySuccess"))
          }else{
            this.$message.warning(res.body.$message)
          }
        });
      }
    },
    updateFixed() {
      // if(this.famaterData()) {
      //   this.$http.post(this.netApi.updateParamInfo, this.form).then(res => {
      //     if(res.body.status == 200) {
      //       this.$message.success('设置配置成功')
      //     }else{
      //       this.$message.warning(res.body.$message)
      //     }
      //   })
      // }
      console('update')
    },
    famaterData() {
      // 场景id
      this.form.sceneId = this.modelScene
      // 通行方式
      this.form.passageWay = this.passageWayOne.join(',')
      // 待机动画
      const imgZd = ['pictureOne', 'pictureTwo', 'pictureThree']
      this.AnimationList.forEach((res, index) => {
        this.form[imgZd[index]] = ''
        this.form[imgZd[index]] = res.url
      })
      // 时间
      let weeks = JSON.parse(JSON.stringify(this.allWeeks))
      weeks.forEach((item, dex1) => {
        item.timeList.forEach((res, dex2) => {
          let start = res.timeQj[0]
          let end = res.timeQj[1]
          let minStart = `${start % 6 * 10 < 10 ? '0' + start % 6 * 10 : start % 6 * 10}`
          let minEnd = `${end % 6 * 10 < 10 ? '0' + end % 6 * 10 : end % 6 * 10}`
          weeks[dex1].timeList[dex2].timeQj = `${Math.floor(start / 6)}:${minStart}-${Math.floor(end / 6)}:${minEnd}`
        })
      })
      let xqs = ['monday', 'tuesday', 'wednesday', 'thursday', 'friday', 'saturday', 'sunday']
      xqs.forEach((xq, index) => {
        let str = ''
        weeks[index].timeList.forEach(res => {
          if(str) {
            str = str + ',' + res.timeQj
          } else {
            str = res.timeQj
          }
        })
        this.form[xq] = str
      })
      let isNotEmpty = this.tryOutNotEmpty()
      return isNotEmpty
    },
    // 非空判断
    tryOutNotEmpty() {
      let isNotEmpty = true
      if(!this.form.checkSuccess) {
        isNotEmpty = false
        this.$message.warning(this.$t('parameter.successcantnull'))
      } else if(!this.form.checkFailure) {
        isNotEmpty = false
        this.$message.warning(this.$t('parameter.failedcantnull'))
      } else if(!this.form.blacklist) {
        isNotEmpty = false
        this.$message.warning(this.$t('parameter.blackListcantnull'))
      } else if(!this.form.logo) {
        isNotEmpty = false
        this.$message.warning(this.$t('parameter.logocannull'))
      } else if(!this.form.pictureOne) {
        isNotEmpty = false
        this.$message.warning(this.$t('parameter.standbycantnull'))
      } else if(!this.form.name) {
        isNotEmpty = false
        this.$message.warning(this.$t('parameter.companynamecantnull'))
      } else if(!this.form.welcome) {
        isNotEmpty = false
        this.$message.warning(this.$t('parameter.welcomecantnull'))
      }
      return isNotEmpty
    },
    closeDialogScene() {
      this.dialogScene = false
    },
    // 删除动画
    removeAnimation(file, fileList) {
      this.AnimationList.forEach((item, index) => {
        if(item.uid === file.uid) {
          this.AnimationList.splice(index, 1)
        }
      })
    },
    // logo上传
    changeLogoFile(file, fileList) {
      if (!/image/.test(file.raw.type)) {
        this.$message.warning(this.$t('parameter.onlyPic'))
        return false
      }
      let This = this;
      let reader = new FileReader();
      reader.readAsDataURL(file.raw);
      reader.onload = function(e) {
        This.logImageUrl = this.result;
        This.form.logo = this.result;
      }
    },
     // 动画图片上传的时候
    changeAnimation(file, fileList) {
      if (!/image/.test(file.raw.type)) {
        this.$message.warning(this.$t('parameter.formatError'))
        return false
      }
      let self = this;
      var reader = new FileReader();
      reader.readAsDataURL(file.raw);
      reader.onload = function(e) {
        if(self.isUp) {
          // 这个就是base64编码了
          self.AnimationList.push({
            name: file.name,
            url: this.result
          })
        }
      }
    },
    // 新增工作时间段
    addTimeTip(dex1, dex2) {
      this.$set(this.allWeeks[dex1].timeList, `${this.allWeeks[dex1].timeList.length}`, {timeQj: [0, 144]})
    },
    // 删除工作时间段
    delTimeTip(dex1, dex2) {
      this.allWeeks[dex1].timeList.splice(dex2, 1)
    },
    // 同步昨天的时间段设置
    tupTime(dex1, dex2) {
      this.allWeeks[dex1].timeList = JSON.parse(JSON.stringify(this.allWeeks[dex1 - 1].timeList))
    },
    // 处理所选时间区间
    filterTimeInterVal(val) {
      let start = val[0]
      let end = val[1]
      let minStart = `${(start % 6 * 10) < 10 ? '0' + start % 6 * 10 : start % 6 * 10}`
      let minEnd = `${(end % 6 * 10 < 10) ? '0' + end % 6 * 10 : end % 6 * 10}`
      return `${Math.floor(start / 6)}:${minStart}-${Math.floor(end / 6)}:${minEnd}`
    },
    // 格式化toolTip
    formatTooltip(val) {
      return val / 100;
    },
    // 格式化设备时间
    formatTimetip(val) {
      let mintime = `${(val % 6 * 10) < 10 ? '0' + val % 6 * 10 : val % 6 * 10}`
      return `${Math.floor(val / 6)}:${mintime}`
    },
    sceneFz(s) {
      this.preventFalse = s.antiFake
      this.liveThingVal = s.checkValue
      this.NcapacityVal = s.similarity
      this.OneCapacityVal = s.visibleRay
    },
    // 快速编辑场景
    editScene(val, scId) {
      this.dialogScene = true

      if(val === 0) {
        this.dialogScenceTitle = '安全场景'
        this.sceneType = 0
      } else {
        this.dialogScenceTitle = '快速场景'
        this.sceneType = 1
      }
      // 在编辑状态且id相等情况下将以前的值给复值
      if(this.$route.query.sn && scId === this.form.sceneId) {
        this.iseditScenec = true
        let s = JSON.parse(this.form.sceneJson)
        this.sceneFz(s)
      } else {
        this.iseditScenec = false
      }
    },
    // 场景获取参数值
    getSceneParam(val) {
      console.log(val)
      this.form.sceneJson = val
      this.iseditScenec = true
    },
    getConfig () {
      // 语言
      let language = new Promise((resolve, reject) => {
        this.$http.get(this.netApi.getLanguages).then(
          res => {
            if (res.body.status == 200) {
              resolve(res.body.data);
            }
          }
        )
      });
      // 时区
      let timeZone = new Promise((resolve, reject) => {
        this.$http.get(this.netApi.getTime).then(
          res => {
            if (res.body.status == 200) {
              resolve(res.body.data);
            }
          }
        )
      });
      // 地区
      let region = new Promise((resolve, reject) => {
        this.$http.get(this.netApi.getPlaces).then(
          res => {
            if (res.body.status == 200) {
              resolve(res.body.data);
            }
          }
        )
      });
      // 日期格式
      let dateFormat = new Promise((resolve, reject) => {
        this.$http.get(this.netApi.getDateFormater).then(
          res => {
            if (res.body.status == 200) {
              resolve(res.body.data);
            }
          }
        )
      });
      let impotType = new Promise((resolve, reject) => {
        this.$http.get(this.netApi.getOutInType).then(
          res => {
            if (res.body.status == 200) {
              resolve(res.body.data);
            }
          }
        )
      });
      // 门禁控制方式
      let contros = new Promise((resolve, reject) => {
        this.$http.get(this.netApi.getContros).then(
          res => {
            if (res.body.status == 200) {
              resolve(res.body.data);
            }
          }
        )
      });
      // 通信方式
      let ways = new Promise((resolve, reject) => {
        this.$http.get(this.netApi.getWays).then(
          res => {
            if (res.body.status == 200) {
              resolve(res.body.data);
            }
          }
        )
      });
      // 场景方式查询
      let scenes = new Promise((resolve, reject) => {
        this.$http.get(this.netApi.getScenes).then(
          res => {
            if (res.body.status == 200) {
              resolve(res.body.data);
            }
          }
        )
      });
      let self = this;
      Promise.all([language, region, timeZone, dateFormat, impotType, contros, ways, scenes]).then((vals) => {
        // self.form = vals[0];
        //语言
        self.languageSel = vals[0];
        self.form.language = self.languageSel[0].categoryNo;
        // //地区
        self.regionSel = vals[1];
        self.form.region = self.regionSel[0].categoryNo
        // 时区
        self.timeZoneSel = vals[2];
        self.form.timeZone = self.timeZoneSel[0].categoryNo
        //时间格式
        self.dateFormatSel = vals[3];
        self.form.dateFormat = self.dateFormatSel[0].categoryNo
        //出入类型
        self.impotTypeSel = vals[4];
        self.form.currentType = self.impotTypeSel[1].categoryNo
        // 门禁控制方式
        self.controlSel = vals[5]
        self.form.accessControl = self.controlSel[0].categoryNo
        // 通行方式
        self.waySel = vals[6]
        self.passageWayOne.push(self.waySel[0].categoryNo)
        // 场景方式
        self.sceneSel = vals[7]
        self.modelScene = self.sceneSel[0].id
      })
    },
    getBase64Image(img) {
      var canvas = document.createElement("canvas");
      canvas.width = img.width;
      canvas.height = img.height;
      var ctx = canvas.getContext("2d");
      ctx.drawImage(img, 0, 0, img.width, img.height);
      var ext = img.src.substring(img.src.lastIndexOf(".") + 1).toLowerCase();
      var dataURL = canvas.toDataURL("image/" + ext);
      return dataURL;
    },
    handleChange () {

    },
    handleRemove () {

    },
    handlePictureCardPreview () {

    },
    handlePreview () {

    }
  }
};



