<template>
  <div>
    <el-dialog
      :title="dialogScenceTitle"
      :visible.sync="isShow"
      :before-close="handleClose"
      width="30%">
      <div class="prevent-s">
        <span>活体防伪相关参数</span>
        <span class="prevent-false">
          <el-switch
              v-model="preventFalse"
              active-text="开"
              inactive-text="关"
              >
          </el-switch>
        </span>
      </div>
      <div class="prevent-value">
        <div class="prevent-value-t">
          <span class="title">活体检测阈值</span>
          <span class="tro">数值越大越严格</span>
        </div>
        <div class="tool-tip">
          <el-row :gutter="20">
            <el-col :span="16">
                 <el-slider v-model="liveThingTip" :format-tooltip="formatTooltip"></el-slider>
            </el-col>
            <el-col :span="8">
              <div class="live-btn">
                <el-input-number size="mini" v-model="liveThingVal" :precision="2" :step="0.01" :min="0" :max="1"></el-input-number>
              </div>
            </el-col>
          </el-row>
        </div>
      </div>

      <div class="prevent-s">
        <span>1:N识别比对相关参数{{liveThingValProp}}</span>
      </div>
      <div class="prevent-value">
        <div class="prevent-value-t">
          <span class="title">相似度阈值</span>
          <span class="tro">相似度大于该值可通过,值越大越严格</span>
        </div>
        <div class="tool-tip">
          <el-row :gutter="20">
            <el-col :span="16">
                 <el-slider v-model="NcapacityTip" :format-tooltip="formatTooltip"></el-slider>
            </el-col>
            <el-col :span="8">
              <div class="live-btn">
                <el-input-number size="mini" v-model="NcapacityVal" :precision="2" :step="0.01" :min="0" :max="1"></el-input-number>
              </div>
            </el-col>
          </el-row>
        </div>
      </div>

       <div class="prevent-s">
        <span>1:1识别比对相关参数</span>
      </div>
      <div class="prevent-value">
        <div class="prevent-value-t">
          <span class="title">可见光阈值</span>
          <span class="tro">相似度大于该值可通过,值越大越严格</span>
        </div>
        <div class="tool-tip">
          <el-row :gutter="20">
            <el-col :span="16">
                 <el-slider v-model="OneCapacityTip" :format-tooltip="formatTooltip"></el-slider>
            </el-col>
            <el-col :span="8">
              <div class="live-btn">
                <el-input-number size="mini" v-model="OneCapacityVal" :precision="2" :step="0.01" :min="0" :max="1"></el-input-number>
              </div>
            </el-col>
          </el-row>
        </div>
      </div>

      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="resetValue" size="small">恢复默认值</el-button>
        <el-button @click="handleClose">取 消</el-button>
        <el-button type="primary" @click="handleConfirm">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: 'liveParam',
  props: {
    isShow: {
      type: Boolean,
      default: true
    },
    iseditScenec: {
      type: Boolean,
      default: false
    },
    sceneType: {
      type: Number,
      default: 0
    },
    dialogScenceTitle: {
      type: String,
      default: '安全场景'
    },
    preventFalseProp: {
      type: Boolean,
      default: true
    },
    liveThingValProp: {
      type: Number,
      debfault: 0.5
    },
    NcapacityValProp: {
      type: Number,
      debfault: 0.5
    },
    OneCapacityValProp: {
      type: Number,
      debfault: 0.5
    },
    NcapacityTipProp: {
      type: Number,
      debfault: 50
    },
    liveThingTipProp: {
      type: Number,
      debfault: 50
    },
    OneCapacityTipProp: {
      type: Number,
      debfault: 50
    }
  },
  data() {
    return {
      preventFalse: true,
      liveThingVal: this.liveThingValProp, // 活体防伪值
      liveThingTip: this.liveThingTipProp,
      NcapacityVal: this.NcapacityValProp, // 1:N识别
      NcapacityTip: this.NcapacityTipProp,
      OneCapacityVal: this.OneCapacityValProp, // 1:1识别
      OneCapacityTip: this.OneCapacityTipProp
    }
  },
  watch: {
    // 监听场景类型
    sceneType(val) {
      if(val === 0 && this.iseditScenec === false) {
        this.liveThingVal = this.NcapacityVal = this.OneCapacityVal = 0.5
      }
      if(val === 0 && this.iseditScenec) {
        this.liveThingVal = this.NcapacityVal = this.OneCapacityVal = 0.5
      }
      if(val === 1 && this.iseditScenec === false) {
        this.liveThingVal = this.NcapacityVal = this.OneCapacityVal = 0.6
      }
    },
    liveThingValProp(val) {
      this.liveThingVal = val
    },
    NcapacityValProp(val) {
      this.NcapacityVal = val
    },
    OneCapacityValProp(val) {
      this.OneCapacityVal = val
    },
    liveThingVal(val) {
      this.liveThingTip = val * 100
    },
    liveThingTip(val) {
      this.liveThingVal = val / 100
    },
    NcapacityVal(val) {
      this.NcapacityTip = val * 100
    },
    NcapacityTip(val) {
      this.NcapacityVal = val / 100
    },
    OneCapacityVal(val) {
      this.OneCapacityTip = val * 100
    },
    OneCapacityTip(val) {
      this.OneCapacityVal = val / 100
    }
  },
  methods: {
    resetValue() {
      if(this.dialogScenceTitle === '安全场景') {
        this.liveThingVal = this.NcapacityVal = this.OneCapacityVal = 0.5
      } else {
        this.liveThingVal = this.NcapacityVal = this.OneCapacityVal = 0.6
      }
    },
    handleClose () {
      this.$emit("active");
    },
    // 格式化toolTip
    formatTooltip(val) {
      return val / 100;
    },
    // 确定按钮
    handleConfirm() {
      this.$emit("active");
      let param = {
        name: this.dialogScenceTitle,
        antiFake: this.preventFalse,
        checkValue: this.liveThingVal,
        similarity: this.NcapacityVal,
        visibleRay: this.OneCapacityVal
      }
      this.$emit('getSceneParam', JSON.stringify(param))
    }
  }
}
</script>

<style>

</style>



